* { 
  box-sizing: border-box;
  padding: 0;
  margin: 0; 
 }

body,
html {
  font-family: "Arial", sans-serif;
  color: #333;
  height: 100%;
  position: relative;
  background-color: #29648a;
}

/* Heading Styling */
h2 {
  text-align: center;
  font-size: 45px;
  color: #b80ccb;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
h4{
  text-align: center;
  font-size: 18px;
  text-wrap: nowrap ;
  padding-top: 10px;
}

/* Main Container */
.Main {
  width: 80%;
  margin: 50px auto;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  padding: 20px;
}

/* Search Section */
.search {
  width: 50%;
  margin: 20px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Input Box */
.Input-container {
  width: 450px;
  height: 55px;
  border-radius: 5px;
  padding-left: 20px;
  font-size: 18px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Button */
button {
  width: 25%;
  height: 55px;
  border-radius: 8px;
  background-color: #007b7f;
  cursor: pointer;
  color: white;
  font-size: 16px;
  border: none;
  transition: background-color 0.3s ease;
  margin-left: 10px;
}

button:hover {
  background-color: #005f63;
}

/* Movie List */
.movie-list {
  width: 90%;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

/* Movie Poster Card */
.movie-poster {
  width: 250px;
  height: 400px;
  border-radius: 10px;
  background-color: rgba(0, 255, 255, 0.1);
  box-shadow: 0 4px 10px rgba(5, 161, 109, 0.626);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.movie-poster:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

/* Image Styling */
img {
  width: 200px;
  height: 250px;
  border-radius: 10px;
}
.loader {
  margin: auto;
}
